<template>
  <div class="teacher">
    <span class="backHome iconfont icon-fanhui" @click="backHome"></span>
    <div class="item" v-for="(item) in teacherData" :key="teacherData[item]">
      <div class="teacherImg clearfix">
          <div class="leftImg left">
           <img :src=" headUrl + item.picture" />

          </div>
        <div class="message left">
          <span class="icon">WEB</span>
          <h4>{{item.nickname}}老师</h4>
          <span>{{item.position}}</span>
        </div>
      </div>
      <div class="teacherMessage clearfix">
        <p>{{item.description}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "teacher",
  data() {
    return {
      teacherData: [],
      headUrl: "https://www.tzweb.vip"
    };
  },
  methods : {
   backHome(){
     this.$router.push('/')
   }
  },
  created() {
    this.$axios.get("https://www.tzweb.vip/api/teacher").then(res => {
      this.teacherData = res.data.data;
    });
  }
};
</script>
<style lang="stylus" scoped>
.teacher {
  background-color: #8249d0;
  background-image:url(https://www.tzweb.vip/static/images/background.png);
  padding: 20px;
  .backHome{
    font-size:35px;
    color:white;
  }
  .item {
    background-color: #ffffff;
    border-radius: 10px;
    padding:10px;
    margin-top:10px;
    .left {
      float: left;
      margin-left: 15px;
    }

    .right {
      float: right;
    }

    .clearfix:after{
        content:'';
        display:block;
        height:0;
        clear: both;
    }
    .teacherImg {
      img {
        width: 150px;
        height: 150px;
      }
    }
  }
  .icon {
    display: inline-block;
    width: 34px;
    height: 20px;
    background-color: #09cbd7;
    text-align: center;
    line-height: 20px;
    color: #fff;
    font-style: normal;
    font-size: 12px;
    border-radius: 5px;
  }
  .teacherMessage{
    font-size: 16px;
    line-height: 30px; 
    text-indent: 2em;
    color: #666;
  }
}
</style>